import React, { memo } from 'react';
import { isEmpty, isArray, merge } from 'lodash';
import { FormControl, FormHelperText } from '@material-ui/core';
import PropTypes from 'prop-types';

import AttachButton from '../AttachButton';
import FilePreviewButton from '../FilePreviewButton';
import styles from './styles';

const FormAttachmentField = ({
  errors, value = [], onChangeValue = () => {},
  disabled,
  uploadFileFunc, onUploadProgress, onUploadSuccess, onUploadError
}) => {
  const onFileChange = (newFile) => {
    uploadFileFunc({
      file: newFile,
      progressHandler: ({ progressPercent }) => {
        const updatedFile = merge(
          newFile, { progressPercent }
        );
        onChangeValue(updatedFile);
        if (onUploadProgress) onUploadProgress(updatedFile);
      }
    })
      .catch(onUploadError)
      .then(result => {
        const updatedFile = merge(newFile, result);
        onChangeValue(updatedFile);
        if (onUploadSuccess) onUploadSuccess(updatedFile);
      });
  };

  let component;
  if (!value || (!value.progressPercent && !value.id)) {
    component = (
      <AttachButton
        accept="image/*,.doc,.docx,.xls,.xlsx,.pdf,.xml"
        onChange={onFileChange}
        disabled={disabled}
      />
    );
  } else {
    component = (
      <FilePreviewButton
        file={value}
        onRemoveFile={fileToRemove => fileToRemove.cancelUpload || onChangeValue(null)}
        disabled={disabled}
      />
    );
  }

  return (
    <FormControl margin="normal" error={!isEmpty(errors)}>
      { component }
      {
        !isEmpty(errors) && (
          <FormHelperText style={styles.errors}>
            {isArray(errors) ? errors.join(', ') : errors}
          </FormHelperText>
        )
      }
    </FormControl>
  );
};

FormAttachmentField.propTypes = {
  errors: PropTypes.any,
  value: PropTypes.object,
  onChangeValue: PropTypes.func,
  disabled: PropTypes.bool,
  uploadFileFunc: PropTypes.func,
  onUploadProgress: PropTypes.func,
  onUploadSuccess: PropTypes.func,
  onUploadError: PropTypes.func
};

export default memo(FormAttachmentField);
