import React, { memo } from 'react';
import { get, isEmpty, sortBy } from 'lodash';
import { compose, withState } from 'recompose';
import { Box } from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import PropTypes from 'prop-types';
import moment from 'moment';

import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import withRequest from '../../../utils/with-request';
import withViewerData from '../../../utils/with-viewer-data';
import EnterpriseUserService from '../../../services/enterprise-user';
import SubscriptionsService from '../../../services/subscriptions';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import Session from '../../../utils/session';
import withWrapper from '../with-wrapper';
import Loading from '../../../components/Loading';
import NoSubscriptionBlock from './NoSubscriptionBlock';
import CurrentSubscriptionBlock from './CurrentSubscriptionBlock';
import InvoicesTable from './InvoicesTable';

const MyPlan = ({
  viewer, setAlert,
  subscriptionData, subscriptionLoading, subscriptionError, subscriptionFetch
}) => {
  let subscriptionBlock;
  const trialExpiration = get(viewer, 'member.company.trial_expiration');
  const subscription = get(subscriptionData, 'subscription');
  if (subscriptionLoading) return <Loading />;

  if (isEmpty(subscriptionError)) {
    const hasActiveSubscription = subscription && (
      new Date(subscription.periodEnd) > new Date()
    );
    subscriptionBlock = hasActiveSubscription ? (
      <CurrentSubscriptionBlock
        setAlert={setAlert}
        subscription={subscription}
        refetch={() => subscriptionFetch(true)}
      />
    ) : (
      <NoSubscriptionBlock setAlert={setAlert} />
    );
  }
  const trialExpirationAt = trialExpiration && moment(trialExpiration);
  const willDowngradeAt = get(subscription, 'willDowngradeAt') && moment(subscription.willDowngradeAt);
  const willCancelAt = get(subscription, 'willCancelAt') && moment(subscription.willCancelAt);
  return (
    <React.Fragment>
      {
        trialExpirationAt && !willDowngradeAt && !willCancelAt &&
          <Box my={2}>
            <Alert severity="warning">
              {`O plano trial vai expirar na data: ${trialExpirationAt.format('DD/MM/YYYY')}`}
            </Alert>
          </Box>
      }
      {
        willDowngradeAt && !willCancelAt &&
          <Box my={2}>
            <Alert severity="info">
              {`Esse plano ter reduo de lugares agendada para data: ${willDowngradeAt.format('DD/MM/YYYY')}`}
            </Alert>
          </Box>
      }
      {
        willCancelAt &&
          <Box my={2}>
            <Alert severity="warning">
              {`Ateno: esse plano ser cancelado na data: ${willCancelAt.format('DD/MM/YYYY')}`}
            </Alert>
          </Box>
      }
      <Box mb={4} mt={2}>
        {subscriptionBlock}
        <InvoicesTable setAlert={setAlert} />
      </Box>
    </React.Fragment>
  );
};

MyPlan.propTypes = {
  viewer: PropTypes.object,
  setAlert: PropTypes.func,
  subscriptionLoading: PropTypes.bool,
  subscriptionData: PropTypes.object,
  subscriptionError: PropTypes.object,
  subscriptionFetch: PropTypes.func
};

const withSubscription = withRequest(SubscriptionsService.get, {
  name: 'subscription',
  options: ({ setAlert }) => ({
    onRequestFinished: async () => {
      const enterpriseUserResponse = await EnterpriseUserService.getEnterpriseUser();
      const enterpriseUser = get(enterpriseUserResponse, 'enterprise_user');
      Session.setUserData({
        ...enterpriseUser,
        ...{ buildings: sortBy(get(enterpriseUser, 'buildings'), ['name']) }
      });
    },
    onRequestError(error) {
      console.error(error);
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.member.role)
  }),
  withViewerData,
  withState('loading', 'setLoading', true),
  withWrapper,
  withSubscription
)(MyPlan);
